<template>
  <div class="login-container">
    <van-form
      ref="formRef"
      @submit="onLogin"
      @failed="onFailed"
      error-message-align="center"
      validate-first
      :show-error="false"
      :show-error-message="false"
    >
      <van-nav-bar
        class="topbar-container"
        title="注册 / 登录"
        left-arrow
        @click-left="$router.back()"
      />
      <van-cell-group>
        <van-field
          v-model="user.mobile"
          icon-prefix="iconfont"
          left-icon=" iconshouji"
          placeholder="请输入手机号"
          name="mobile"
          required
          :rules="formRules.mobile"
        />
        <van-field
          v-model="user.code"
          clearable
          icon-prefix="iconfont"
          left-icon=" iconyanzhengma"
          placeholder="请输入验证码"
          name="code"
          required
          :rules="formRules.code"
        >
          <template #button>
            <van-button
              class="send-btn"
              size="small"
              round
              v-if="codeDidabled"
              @click.prevent="onSendSms"
              :loading="isLoading"
              >获取验证码</van-button
            >
            <van-count-down
              v-else
              :time="60 * 1000"
              format="ss s"
              @finish="codeDidabled = true"
            />
          </template>
        </van-field>
      </van-cell-group>
      <div class="btn">
        <van-button type="info" block :disabled="isDisabled">登 录</van-button>
      </div>
    </van-form>
    <div class="info">账号：13911111111，验证码：246810</div>
  </div>
</template>

<script>
import { Toast } from 'vant';
import { login, getCode } from '@/api/user.js';
import { mapMutations } from 'vuex';

export default {
  name: 'Login',
  computed: {},
  data() {
    return {
      // 表单验证规则
      formRules: {
        mobile: [
          { required: true, message: '请输入手机号码' },
          {
            pattern: /^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/,
            message: '请输入正确手机号码',
          },
        ],
        code: [
          { required: true, message: '请输入验证码' },
          { pattern: /^\d{6}$/, message: '请输入6位验证码' },
        ],
      },
      // 获取验证码是否loading
      isLoading: false,
      // 登录按钮是否禁用
      isDisabled: false,
      // 获取验证码是否显示
      codeDidabled: true,
      user: {
        mobile: '',
        code: '',
      },
    };
  },
  methods: {
    // 存储token
    ...mapMutations(['setToken', 'removeCachePage']),
    // 提交表单 (验证成功)
    async onLogin() {
      Toast.loading({
        message: '登陆中...',
        forbidClick: true,
        loadingType: 'spinner',
        duration: 0,
      });
      try {
        const { data } = await login(this.user);
        // 将获取的token信息存储 到vuex和本地
        this.setToken(data.data);
        // 移除keep-alive中layout组件的保持状态
        this.removeCachePage('Layout');
        Toast.success('登录成功');
        this.$router.push(this.$route.query.toPath || '/');
      } catch (e) {
        console.log(e);
        Toast.fail('登录失败,请确认验证码');
      }
    },
    // 表单验证失败
    onFailed({ errors }) {
      Toast({
        message: errors[0].message,
        position: 'top',
      });
    },
    // 点击请求验证码
    async onSendSms() {
      try {
        await this.$refs.formRef.validate('mobile');
        this.isLoading = true;
        const data = await getCode(this.user.mobile);

        if (data.status === 200) {
          this.codeDidabled = false;
        }
      } catch (e) {
        let message = '';
        if (e && e.response && e.response.status === 429) {
          message = '操作频繁 请稍后再试';
        } else if (e.name) {
          message = e.message;
        }

        Toast({
          message,
          position: 'top',
        });
      }
      this.isLoading = false;
    },
  },
};
</script>

<style lang="less" scoped>
.send-btn {
  width: 90px;
  height: 26px;
  background-color: #eee;
  .van-button__text {
    font-size: 11px;
  }
}
.btn {
  padding: 26px 16px;
  .van-button {
    border: none;
    .van-button__text {
      font-size: 15px;
    }
  }
}

.van-loading__spinner {
  width: 15px !important;
  height: 20px !important;
}
.info {
  text-align: center;
  font-size: 14px;
  color: #aaa;
}
</style>
